.Preloader {
  position: absolute;
  width: 100vw;
  height: 100vh !important;
  background-color: #0A221D;
  z-index: 10;
  opacity: 0;
  width: 300px;
  -webkit-transition: all 1.4s cubic-bezier(0.81, 0.42, 0.33, 1);
  transition: all 1.4s cubic-bezier(0.81, 0.42, 0.33, 1);
  pointer-events: none; }

@media screen and (max-width: 992px) {
  .Preloader {
    width: 0; } }

.Preloader__content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%; }

.Preloader__brand {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Geo Sans';
  font-size: 40px;
  color: #fff;
  text-transform: uppercase;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
  opacity: 0; }

.Preloader__brand span {
  display: block; }

.Preloader__bar {
  position: relative;
  width: 100vw;
  opacity: 0;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  margin-top: 70px;
  border-radius: 2px; }

.Preloader__bar .number {
  font-family: 'Lowan';
  display: block;
  position: absolute;
  left: 0;
  top: -50px;
  width: 1%;
  display: flex;
  justify-content: flex-end;
  font-size: 25px;
  color: #9D8855;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding-left: 30px; }

@media screen and (max-width: 576px) {
  .Preloader__bar .number {
    padding-left: 40px; } }

.Preloader__bar:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100vw;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  height: 1.6px;
  opacity: 0;
  background-color: #9D8855; }

.Preloader__bar-percentage {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  height: 13px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: #9D8855;
  width: 1%; }

.Preloader__bar img {
  position: absolute;
  margin: 0 auto;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  left: 50%; }

.Preloader .text-transition_inner div {
  width: 100%;
  text-align: center; }

.Preloader .text-transition {
  text-align: center; }

.Preloader[data-show="true"] {
  width: 100vw;
  opacity: 1;
  pointer-events: all; }

.Preloader[data-show="true"] .Preloader__bar, .Preloader[data-show="true"] .Preloader__brand {
  opacity: 1; }
